</template>
  
  <script>
import { add } from "../api/fenlei";
import { beforeremove } from "../api/fenlei";
import { remove } from "../api/fenlei";
import { list } from "../api/fenlei";

import tabs from "@/components/tabs.vue";
export default {
  components: {
    tabs,
  },
  data() {
    return {
      addshow: false,
      bjshow: false,
      list: [
        {
          title: "题材",
          num: "0",
        },
        {
          title: "难度",
          num: "1",
        },
        {
          title: "人数",
          num: "2",
        },
        {
          title: "水平",
          num: "3",
        },
      ],
      list2: [
        {
          title: "平台通知",
          num: "0",
        },
        {
          title: "系统通知",
          num: "1",
        },
      ],
      type11: "",
      type22: "",
      text: "",
      ndList: [],
      rsList: [],
      tcList: [],
      wjdjList: [],
      newlist: [],
      activeName: "0",
      formInline: {
        user: "",
        region: "",
      },
      tags: [
        { name: "标签一", type: "" },
        { name: "标签二", type: "success" },
        { name: "标签三", type: "info" },
        { name: "标签四", type: "warning" },
        { name: "标签五", type: "danger" },
      ],
      arr: "",
      id: [],
    };
  },
  watch: {},
  computed:{
    num(){
      return ' ' +2
    }
  },
  methods: {
    typetwo(e) {
      console.log(this.type22);
      if (e == 0) {
        beforeremove({ fl: e }).then((res) => {
          this.newlist = res.data;
        });

        console.log(this.newlist);
      } else if (e == 1) {
        beforeremove({ fl: e }).then((res) => {
          let arr = res.data;
          let newarr = arr.map((item) => ({
            name: item.dict_label,
            id: item.id,
          }));
          this.newlist = newarr;
        });
      } else if (e == 2) {
        beforeremove({ fl: e }).then((res) => {
          let arr = res.data;
          let newarr = arr.map((item) => ({
            name: item.dict_label,
            id: item.id,
          }));
          this.newlist = newarr;
        });
      } else if (e == 3) {
        beforeremove({ fl: e }).then((res) => {
          let arr = res.data;
          let newarr = arr.map((item) => ({
            name: item.dict_label,
            id: item.id,
          }));
          this.newlist = newarr;
        });
      }
    },
    fl(val) {},
    handleClick({ name }) {
      console.log(name);
      this.activeName = name;
      if (this.activeName == 0) {
        list({ type: 0 }).then((ress) => {
          this.ndList = ress.data.ndList;
          this.rsList = ress.data.rsList;
          this.tcList = ress.data.tcList;
          this.wjdjList = ress.data.wjdjList;
        });
      }
      if (this.activeName == 1) {
        list({ type: 1 }).then((res) => {
          console.log(res);
          this.tcList = res.data.yhlxList;
        });
      }
      if (this.activeName == 2) {
        list({ type: 2 }).then((res) => {
          console.log(res);
          this.tcList = res.data.ptList;
          this.ndList = res.data.xtList;
        });
      }
      if (this.activeName == 3) {
        list({ type: 3 }).then((res) => {
          console.log(res);
          this.tcList = res.data.roomTypeList;
        });
      }
      if (this.activeName == 4) {
        list({ type: 4 }).then((res) => {
          console.log(res);
          this.tcList = res.data.pinpaiList;
        });
      }
    },
    bj() {
      if (this.activeName == 0) {
        beforeremove({ fl: 1, type: 0 }).then((res) => {});
        this.bjshow = true;
        this.type22 = "0";
        this.newlist = this.tcList;
      }
      if (this.activeName == 1) {
        beforeremove({ type: 1, fl: 0 }).then((res) => {
          console.log(res.data);
          this.newlist = res.data;
        });
        this.bjshow = true;
        this.type22 = "1";
      }
      if (this.activeName == 2) {
        beforeremove({ fl: 3 }).then((res) => {});
        this.bjshow = true;
        this.type22 = "2";
        this.newlist = this.tcList;
      }
      if (this.activeName == 3) {
        beforeremove({ fl: 4 }).then((res) => {});
        this.bjshow = true;
        this.type22 = "3";
        this.newlist = this.tcList;
      }
      if (this.activeName == 4) {
        beforeremove({ fl: 5 }).then((res) => {});
        this.bjshow = true;
        this.type22 = "4";
        this.newlist = this.tcList;
      }
    },
    close() {
      this.addshow = false;
    },
    close2() {
      this.bjshow = false;
    },
    cancel() {
      this.addshow = false;
      this.bjshow = false;
    },
    handleClose(tag) {
      if (this.activeName == 0) {
        let arrs = this.id;
        arrs.push({
          id: tag.id,
        });
        this.id = arrs;
        this.newlist.splice(this.newlist.indexOf(tag), 1);
        // let list2 = [];
        // for (let i in this.newlist) {
        //   list2.push({
        //     name: this.newlist[i].name,
        //   });
        // }
        // console.log(list2);
        // this.newlist.forEach((item, index) => {
        //   item.dict_label = item.name;
        // });
        this.arr = this.newlist;
        console.log(this.arr);
      }
      if (this.activeName == 1) {
        let arrs = this.id;
        arrs.push({
          id: tag.id,
        });
        this.id = arrs;
        this.newlist.splice(this.newlist.indexOf(tag), 1);
        this.newlist.forEach((item, index) => {
          item.name = item.dict_label;
        });
        this.arr = this.newlist;
        console.log(this.arr);
      }
    },
    addshows() {
      this.addshow = true;
    },
    confirm() {
      if (this.addshow == true && this.activeName == 0) {
        add({ type: 0, fl: this.type11, name: this.text }).then((res) => {
          list({ type: 0 }).then((ress) => {
            this.ndList = ress.data.ndList;
            this.rsList = ress.data.rsList;
            this.tcList = ress.data.tcList;
            this.wjdjList = ress.data.wjdjList;
          });
        });
        this.addshow = false;
      }
      if (this.addshow == true && this.activeName == 1) {
        add({ type: 1, name: this.text }).then((res) => {
          list({ type: 1 }).then((ress) => {
            this.tcList = ress.data.yhlxList;
          });
        });
        this.addshow = false;
      }
      if (this.addshow == true && this.activeName == 2) {
        add({ type: 2, fl: this.type11, name: this.text }).then((res) => {
          list({ type: 2 }).then((ress) => {
            this.tcList = ress.data.ptList;
            this.ndList = ress.data.xtList;
          });
        });
        this.addshow = false;
      }
      if (this.addshow == true && this.activeName == 3) {
        add({ type: 3, fl: this.type11, name: this.text }).then((res) => {
          list({ type: 3 }).then((res) => {
            console.log(res);
            this.tcList = res.data.roomTypeList;
          });
        });
        this.addshow = false;
      }
      if (this.addshow == true && this.activeName == 4) {
        add({ type: 4, fl: this.type11, name: this.text }).then((res) => {
          list({ type: 4 }).then((res) => {
            console.log(res);
            this.tcList = res.data.pinpaiList;
          });
        });
        this.addshow = false;
      }

      if (this.bjshow == true && this.activeName == 0) {
        let params = {
          deleId: this.id,
          fl: Number(this.type22),
          type: 0,
          updateId: this.arr,
        };
        remove(params).then((res) => {
          console.log(res);
          list({ type: 0 }).then((res) => {
            this.bjshow = false;
            this.ndList = res.data.ndList;
            this.rsList = res.data.rsList;
            this.tcList = res.data.tcList;
            this.wjdjList = res.data.wjdjList;
          });
        });
      }
      if (this.bjshow == true && this.activeName == 1) {
        let params = {
          deleId: this.id,
          fl: 0,
          type: 1,
          updateId: this.arr,
        };
        remove(params).then((res) => {
          console.log(res);
          list({ type: 1 }).then((res) => {
            console.log(res);
            this.tcList = res.data.yhlxList;
            this.bjshow = false;
          });
        });
      }
    },
  },
  watch: {},
  mounted() {
    list({ type: 0 }).then((res) => {
      this.ndList = res.data.ndList;
      this.rsList = res.data.rsList;
      this.tcList = res.data.tcList;
      this.wjdjList = res.data.wjdjList;
    });
  },
