import request from '../utils/request'
export function add(params) {
    return request ({
        url: 'api/ktv/classify/add?',
        method: 'GET',
        params
    })
}
export function remove(data) {
    return request ({
        url: '/api/ktv/classify/update',
        method: 'POST',
        data
    })
}
export function beforeremove(params) {
    return request ({
        url: '/api/ktv/classify/updateHcz?',
        method: 'GET',
        params
    })
}
export function list(params) {
    return request ({
        url: '/api/ktv/classify/list?',
        method: 'GET',
        params
    })
}